:root {
    --body-font: "Jost", sans-serif;
    --body-color: #fff;
    --para-color: #2c3655;
    --head-color: #0a1023;
    --head-alt-color: #055c2d;
    --hover-color: #1a4dbe
}

.collapsing {
    transition: .3s
}

html {
    scroll-behavior: smooth;
    overflow-x: hidden
}

body {
    background-color: var(--body-color);
    font-size: 18px;
    line-height: 30px;
    font-weight: 400;
    overflow-x: hidden
}

body,
h1,
h2,
h3,
h4,
h5,
h6 {
    padding: 0;
    margin: 0
}

h1,
h1>a,
h2,
h2>a,
h3,
h3>a,
h4,
h4>a,
h5,
h5>a,
h6,
h6>a {
    color: var(--head-color);
    font-weight: 600;
    text-transform: none;
    transition: all .3s;
    -webkit-transition: all .3s;
    -moz-transition: all .3s;
    -ms-transition: all .3s;
    -o-transition: all .3s
}

h1>a:hover,
h2>a:hover,
h3>a:hover,
h4>a:hover,
h5>a:hover,
h6>a:hover {
    color: var(--body-color)
}

a,
a:focus,
a:hover {
    text-decoration: none;
    outline: none;
    color: var(--para-color)
}

img {
    max-width: 100%;
    height: auto
}

.max-un {
    max-width: unset
}

ol,
ul {
    padding: 0;
    margin: 0;
    list-style: none
}

input:focus,
textarea:focus {
    outline: none;
    box-shadow: none
}

button {
    border: none;
    cursor: pointer
}

input,
textarea {
    padding: 10px 20px;
    color: #1e266d;
    width: 100%;
    height: 50px;
    background: #f5faff;
    border: 1px solid #eef1ff;
    border-radius: 5px
}

input::-webkit-inner-spin-button,
input::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0
}

input[type=number] {
    -moz-appearance: textfield
}


.select {
    position: relative;
    background-color: transparent
}

.select:after {
    content: "";
    vertical-align: 0;
    background-image: url(../../images/icon/menu-down.png);
    background-repeat: no-repeat;
    border: none;
    width: 40px;
    height: 11px
}

select {
    -webkit-appearance: none;
    padding: 10px !important;
    background-color: transparent;
    background: transparent;
    padding: 10px 0;
    border: none;
    cursor: pointer;
    outline: none
}

textarea {
    min-height: 150px;
    resize: none
}

span {
    display: inline-block
}

.table td,
.table th,
.table thead th {
    border: none
}

.d-grid {
    display: grid !important
}

.b-none {
    border: none !important
}

iframe {
    border: 0;
    width: 100%
}

.shadow-none {
    box-shadow: none
}

