::-webkit-scrollbar-track {
    background: #00003e
}

::-webkit-scrollbar {
    width: 6px
}

::-webkit-scrollbar-button,
::-webkit-scrollbar-thumb {
    background-color: #5732c6
}

.scrollToTop {
    position: fixed;
    bottom: 0;
    right: 30px;
    width: 45px;
    height: 45px;
    background-color: var(--hover-color);
    border-radius: 5px;
    color: #fff;
    line-height: 45px;
    font-size: 20px;
    text-align: center;
    z-index: 9;
    cursor: pointer;
    transition: all 1s;
    transform: translateY(100%)
}

.scrollToTop.active {
    bottom: 30px;
    transform: translateY(0)
}

.scrollToTop.active,
.scrollToTop:hover {
    color: var(--body-color)
}

.icon-svg {
    position: relative
}

.icon-svg img {
    opacity: 1;
    position: absolute
}

.icon-svg svg {
    position: absolute;
    stroke-dashoffset: 1500;
    stroke-dasharray: 1500;
    transform: scale(1);
    transition: transform .3s, fill .5s;
    opacity: 0;
    visibility: hidden
}

.icon-svg:hover svg {
    visibility: visible;
    opacity: 1;
    stroke-dashoffset: 0;
    fill: transparent;
    transition: transform .3s, fill .5s, stroke-dashoffset 6s
}

.icon-svg:hover img {
    opacity: 0;
    visibility: hidden
}

.accordion-button:focus {
    box-shadow: none
}

.animated {
    animation-duration: 1s;
    animation-fill-mode: both
}

@keyframes fadeInDown {
    0% {
        opacity: 0;
        transform: translate3d(0, -100%, 0)
    }

    to {
        opacity: 1;
        transform: none
    }
}

.fadeInDown {
    animation-name: fadeInDown
}

.cmn-btn {
    padding: 10px 30px;
    font-weight: 500;
    text-align: center;
    background: var(--hover-color);
    color: var(--body-color);
    border: 1px solid var(--hover-color);
    transition: .3s;
    border-radius: 50px
}

.cmn-btn.second,
.cmn-btn:hover {
    color: #0a1023;
    background: transparent;
    border: 1px solid #848894
