.preloader {
    align-items: center;
    cursor: default;
    display: flex;
    height: 100%;
    justify-content: center;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 9999999
}

.preloader .animation-preloader {
    z-index: 1000
}

.preloader .animation-preloader .spinner {
    animation: spinner 1s linear infinite;
    border-radius: 50%;
    border: 3px solid rgba(0, 0, 0, .2);
    border-top: 3px solid var(--hover-color);
    height: 144px;
    margin: 0 auto;
    width: 144px
}

.preloader p {
    font-size: 18px;
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: 8px;
    color: var(--hover-color)
}

.preloader .loader {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    font-size: 0;
    z-index: 1;
    pointer-events: none
}

.preloader .loader .row {
    height: 100%
}

.preloader .loader .loader-section {
    padding: 0
}

.preloader .loader .loader-section .bg {
    background-color: #f5faff;
    height: 100%;
    left: 0;
    width: 100%;
    transition: all .8s cubic-bezier(.77, 0, .175, 1) !important
}

.preloader.loaded .animation-preloader {
    opacity: 0;
    transition: .3s ease-out
}

.preloader.loaded .loader-section .bg {
    width: 0;
    transition: allcubic-bezier(.1, .1, .1, 1) .7s .3s !important
}

@keyframes spinner {
    to {
        transform: rotate(1turn)
    }
}

